import { el, setChildren } from 'redom';
import isEmail from 'validator/lib/isEmail';
import Inputmask from 'inputmask';
import creditCardType from 'credit-card-type';
import visa from './assets/images/visa.svg';
import mastercard from './assets/images/mastercard.svg';
import mir from './assets/images/mir.svg';
import amex from './assets/images/amex.svg';
import diners from './assets/images/diners.svg';
import unionPay from './assets/images/unionpay.svg';
import maestro from './assets/images/maestro.svg';
import discover from './assets/images/discover.svg';
import jcb from './assets/images/jcb.svg';
import elo from './assets/images/elo.svg';

const valid = require('card-validator');

const $container = el('div.container.py-5');

setChildren(window.document.body, $container);
setChildren($container, [
  el('form#form.d-flex.flex-wrap.justify-content-between', {
    style: 'width: 500px;',
  }, [
    el('legend', 'Форма онлайн-оплаты'),
    el('fieldset.w-100.d-flex.flex-wrap', [
      el('label.form-label.w-100', {
        for: 'number',
      }, 'Номер карты'),
      el('div', {
        id: 'card-logo',
        style: 'width:37.6px; height:37.6px; margin-right: 10px;',
      }, [
        el('img', {
          id: 'img',
          style: 'width: 100%;height:auto;object-fit:contain;',
        }),
      ]),
      el('input.form-control.mb-3.flex-grow-1.w-auto', {
        type: 'text',
        id: 'number',
        placeholder: '0000 0000 0000 0000',
      }),
    ]),
    el('fieldset', {
      style: 'width: 48%;',
    }, [
      el('label.form-label', {
        for: 'date',
      }, 'Дата окончания действия карты'),
      el('input.form-control.mb-3', {
        type: 'text',
        id: 'date',
        placeholder: '00/00',
      }),
    ]),
    el('fieldset', {
      style: 'width: 48%;',
    }, [
      el('label.form-label', {
        for: 'cvc',
      }, 'CVC/CVV'),
      el('input.form-control.mb-3', {
        type: 'text',
        id: 'cvc',
        placeholder: '000',
      }),
    ]),
    el('fieldset.w-100', [
      el('label.form-label', {
        for: 'email',
      }, 'E-mail'),
      el('input.form-control.mb-3', {
        type: 'text',
        id: 'email',
        placeholder: 'example@email.com',
      }),
    ]),
    el('button.btn.btn-primary', {
      id: 'btn',
      type: 'submit',
      disabled: true,
    }, 'Оплатить'),
  ]),
]);

const $cardNumber = document.getElementById('number');
const $cardDate = document.getElementById('date');
const $cardCvc = document.getElementById('cvc');
const $email = document.getElementById('email');
const $button = document.getElementById('btn');
const $cardLogo = document.getElementById('img');

function validationStyle(input, validation) {
  if (validation) {
    input.classList.remove('is-invalid');
    input.classList.add('is-valid');
  } else {
    input.classList.remove('is-valid');
    input.classList.add('is-invalid');
  }
}

function isValidInputs() {
  const inputs = Array.from(document.querySelectorAll('input'));

  if (inputs.every((input) => input.classList.contains('is-valid'))) {
    $button.disabled = false;
  } else {
    $button.disabled = true;
  }
}

const maskNumber = new Inputmask('9999 9999 9999 9999');
maskNumber.mask($cardNumber);

$cardNumber.addEventListener('input', () => {
  $cardNumber.value = $cardNumber.value
    .replace(/[^0-9]/g, '')
    .replace(/\B(?=(\d{4})+(?!\d))/g, ' ');

  if (creditCardType($cardNumber.value)[0] && $cardNumber.inputmask.unmaskedvalue().length >= 4) {
    const cardType = creditCardType($cardNumber.value)[0].type;

    switch (cardType) {
      case 'visa':
        $cardLogo.setAttribute('src', visa);
        break;
      case 'mastercard':
        $cardLogo.setAttribute('src', mastercard);
        break;
      case 'mir':
        $cardLogo.setAttribute('src', mir);
        break;
      case 'american-express':
        $cardLogo.setAttribute('src', amex);
        break;
      case 'diners-club':
        $cardLogo.setAttribute('src', diners);
        break;
      case 'unionpay':
        $cardLogo.setAttribute('src', unionPay);
        break;
      case 'maestro':
        $cardLogo.setAttribute('src', maestro);
        break;
      case 'discover':
        $cardLogo.setAttribute('src', discover);
        break;
      case 'jcb':
        $cardLogo.setAttribute('src', jcb);
        break;
      case 'elo':
        $cardLogo.setAttribute('src', elo);
        break;
      default:
        $cardLogo.setAttribute('src', '');
    }
  } else {
    $cardLogo.setAttribute('src', '');
  }
});

$cardNumber.addEventListener('blur', () => {
  const isValidInput = valid.number($cardNumber.inputmask.unmaskedvalue()).isValid;
  validationStyle($cardNumber, isValidInput);
  isValidInputs();
});

const maskDate = new Inputmask('99/99');
maskDate.mask($cardDate);

$cardDate.addEventListener('input', () => {
  $cardDate.value = $cardDate.value
    .replace(/[^0-9]/g, '')
    .replace(/\B(?=(\d{2})+(?!\d))/g, '/');
});

$cardDate.addEventListener('blur', () => {
  const isValidInput = valid.expirationDate($cardDate.inputmask.unmaskedvalue()).isValid;
  validationStyle($cardDate, isValidInput);
  isValidInputs();
});

const maskCvc = new Inputmask('999');
maskCvc.mask($cardCvc);

$cardCvc.addEventListener('input', () => {
  $cardCvc.classList.remove('is-invalid');

  $cardCvc.value = $cardCvc.value
    .replace(/[^0-9]/g, '');
});

$cardCvc.addEventListener('blur', () => {
  const isValidInput = valid.cvv($cardCvc.inputmask.unmaskedvalue(), 3).isValid;
  validationStyle($cardCvc, isValidInput);
  isValidInputs();
});

$email.addEventListener('blur', () => {
  const isValidInput = isEmail($email.value);
  validationStyle($email, isValidInput);
  isValidInputs();
});

const inputs = Array.from(document.querySelectorAll('input'));

inputs.forEach((input) => {
  input.addEventListener('input', () => {
    input.classList.remove('is-invalid', 'is-valid');
  });
});
